/*======================
    404 page
=======================*/

.page_404 {
    display: flex;
    width: 100vw;
    padding: 40px 0;
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: center;

    * {
        font-family: sans-serif !important;
    }
}

.page_404 img {
    width: 100%;
}

.four_zero_four_bg {

    //background-image: url("./assets/animation.gif");
    /* background-image: url("https://i.pinimg.com/originals/a8/12/1a/a8121abee959e18cbad25ad4046f76d8.gif");
width: 700px;
height: 800px;
background-blend-mode: overlay;
background-position: center;
background-repeat: no-repeat; */
    img {
        margin: 30px 0px;
        width: 500px;

        @media screen and (max-width: 992px) {
            width: 100%;
            margin-top: 0px;
        }
    }
}

h1 {
    font-size: 80px;
    font-family: sans-serif;
    margin-bottom: 50px !important;
}



.link_404 {
    color: #fff !important;
    padding: 10px 20px;
    background: black;
    text-decoration: none;
    margin: 20px 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 250px;
    height: 50px;
    font-weight: bold;
}

.contant_box_404 {
    margin-top: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contant_box_404 .title {
    font-size: 36px;
    font-weight: bold;
    letter-spacing: -0.9px;
}

.contant_box_404 .description {
    margin: 10px 0px;
    max-width: 800px;
    font-size: 22px;
    letter-spacing: -0.5px;
}